import React, { useState, useEffect } from 'react';
import {
  Card,
  Select,
  Typography,
  Space,
  Grid,
  Statistic,
  Alert,
  Divider,
  Button,
  Switch
} from '@arco-design/web-react';
import {
  IconVideoCamera,
  IconStorage,
  IconClockCircle,
  IconSun,
  IconMoon,
  IconInfoCircle,
  IconCalendar,
  IconDashboard,
  IconFile,
  IconSettings,
  IconCheck,
  IconExclamationCircle,
  IconApps,
  IconSafe,
  IconThumbUp
} from '@arco-design/web-react/icon';
import './App.css';

const { Title, Text, Paragraph } = Typography;
const { Row, Col } = Grid;

// 设备数据接口
interface Device {
  id: string;
  name: string;
  brand: string;
  formats: Format[];
}

interface Format {
  name: string;
  frameRates: FrameRate[];
}

interface FrameRate {
  fps: number;
  settings: Setting[];
}

interface Setting {
  name: string;
  bitrate: number;
  resolution: string;
}

// 设备数据
const devices: Device[] = [
  {
    id: 'sony-fx30',
    name: 'Sony FX30',
    brand: 'Sony',
    formats: [
      {
        name: 'XAVC S-I 4K',
        frameRates: [
          {
            fps: 24,
            settings: [
              { name: '标准', bitrate: 600, resolution: '3840x2160' },
              { name: '高质量', bitrate: 800, resolution: '3840x2160' }
            ]
          },
          {
            fps: 30,
            settings: [
              { name: '标准', bitrate: 600, resolution: '3840x2160' },
              { name: '高质量', bitrate: 800, resolution: '3840x2160' }
            ]
          }
        ]
      },
      {
        name: 'XAVC S 4K',
        frameRates: [
          {
            fps: 24,
            settings: [
              { name: '标准', bitrate: 100, resolution: '3840x2160' },
              { name: '高质量', bitrate: 150, resolution: '3840x2160' }
            ]
          },
          {
            fps: 30,
            settings: [
              { name: '标准', bitrate: 100, resolution: '3840x2160' },
              { name: '高质量', bitrate: 150, resolution: '3840x2160' }
            ]
          }
        ]
      }
    ]
  },
  {
    id: 'canon-r5',
    name: 'Canon EOS R5',
    brand: 'Canon',
    formats: [
      {
        name: 'Canon RAW 8K',
        frameRates: [
          {
            fps: 30,
            settings: [
              { name: '标准', bitrate: 2600, resolution: '8192x4320' }
            ]
          }
        ]
      },
      {
        name: 'H.265 4K',
        frameRates: [
          {
            fps: 30,
            settings: [
              { name: '标准', bitrate: 230, resolution: '3840x2160' },
              { name: '高质量', bitrate: 470, resolution: '3840x2160' }
            ]
          },
          {
            fps: 60,
            settings: [
              { name: '标准', bitrate: 340, resolution: '3840x2160' }
            ]
          }
        ]
      }
    ]
  },
  {
    id: 'iphone-14-pro',
    name: 'iPhone 14 Pro',
    brand: 'Apple',
    formats: [
      {
        name: 'ProRes 4K',
        frameRates: [
          {
            fps: 24,
            settings: [
              { name: 'ProRes 422', bitrate: 1100, resolution: '3840x2160' },
              { name: 'ProRes 422 HQ', bitrate: 1650, resolution: '3840x2160' }
            ]
          },
          {
            fps: 30,
            settings: [
              { name: 'ProRes 422', bitrate: 1375, resolution: '3840x2160' },
              { name: 'ProRes 422 HQ', bitrate: 2060, resolution: '3840x2160' }
            ]
          }
        ]
      },
      {
        name: 'H.265 4K',
        frameRates: [
          {
            fps: 24,
            settings: [
              { name: '标准', bitrate: 85, resolution: '3840x2160' }
            ]
          },
          {
            fps: 30,
            settings: [
              { name: '标准', bitrate: 100, resolution: '3840x2160' }
            ]
          }
        ]
      }
    ]
  }
];

// 存储容量选项
const storageOptions = [
  { value: 32, label: '32GB' },
  { value: 64, label: '64GB' },
  { value: 128, label: '128GB' },
  { value: 256, label: '256GB' },
  { value: 512, label: '512GB' },
  { value: 1024, label: '1TB' },
  { value: 2048, label: '2TB' }
];

function App() {
  const [theme, setTheme] = useState('dark');
  const [selectedDevice, setSelectedDevice] = useState<Device | null>(null);
  const [selectedFormat, setSelectedFormat] = useState<Format | null>(null);
  const [selectedFrameRate, setSelectedFrameRate] = useState<FrameRate | null>(null);
  const [selectedSetting, setSelectedSetting] = useState<Setting | null>(null);
  const [storageCapacity, setStorageCapacity] = useState<number>(256);
  
  // 计算结果
  const [maxRecordingTime, setMaxRecordingTime] = useState<number>(0);
  const [dataRate, setDataRate] = useState<number>(0);
  const [fileSize, setFileSize] = useState<number>(0);

  // 重置选择
  const resetSelections = (level: 'device' | 'format' | 'frameRate') => {
    if (level === 'device') {
      setSelectedFormat(null);
      setSelectedFrameRate(null);
      setSelectedSetting(null);
    } else if (level === 'format') {
      setSelectedFrameRate(null);
      setSelectedSetting(null);
    } else if (level === 'frameRate') {
      setSelectedSetting(null);
    }
  };

  // 切换主题
  useEffect(() => {
    if (theme === 'dark') {
      document.body.setAttribute('arco-theme', 'dark');
    } else {
      document.body.removeAttribute('arco-theme');
    }
  }, [theme]);

  // 计算录制时间和数据量
  useEffect(() => {
    if (selectedSetting) {
      const bitrateMbps = selectedSetting.bitrate;
      const storageGB = storageCapacity;
      
      // 计算最大录制时间（分钟）
      const maxTimeMinutes = (storageGB * 1024 * 8) / (bitrateMbps * 60);
      setMaxRecordingTime(Math.floor(maxTimeMinutes));
      
      // 计算数据速率（MB/s）
      const dataRateMBps = bitrateMbps / 8;
      setDataRate(dataRateMBps);
      
      // 计算 1 分钟文件大小（MB）
      const fileSizeMB = dataRateMBps * 60;
      setFileSize(fileSizeMB);
    } else {
      setMaxRecordingTime(0);
      setDataRate(0);
      setFileSize(0);
    }
  }, [selectedSetting, storageCapacity]);

  return (
    <div className="app">
      <div className="container">
        <div className="header">
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Title heading={1}>
              <IconVideoCamera style={{ marginRight: 8, color: 'var(--feishu-primary)' }} />
              视频数据量计算器
            </Title>
            <Switch
              checked={theme === 'dark'}
              onChange={(checked) => setTheme(checked ? 'dark' : 'light')}
              checkedIcon={<IconMoon />}
              uncheckedIcon={<IconSun />}
            />
          </div>
          <Paragraph style={{ fontSize: 16, color: 'var(--feishu-text-2)', marginTop: 8 }}>
            选择录制设备和存储容量，计算最大录制时间和数据使用量
          </Paragraph>
        </div>

        {/* 使用飞书布局规范的栅格系统 */}
        <Row gutter={[{ xs: 12, sm: 16, md: 20, lg: 24, xl: 24 }, { xs: 12, sm: 16, md: 20, lg: 24, xl: 24 }]}>
          {/* 响应式布局：小屏幕占满宽度，大屏幕占一半 */}
          <Col xs={24} sm={24} md={12} lg={12} xl={12} className="layout-col">
            <Card 
              title={
                <Space>
                  <IconVideoCamera style={{ color: 'var(--feishu-primary)' }} />
                  录制配置
                </Space>
              }
              style={{ height: '100%' }}
              bordered={false}
              hoverable
            >
              <Space direction="vertical" size="large" style={{ width: '100%' }}>
                {/* 设备选择 */}
                <div className="form-item">
                  <div className="form-label">选择设备</div>
                  <Select
                    placeholder="请选择录制设备"
                    style={{ width: '100%' }}
                    value={selectedDevice?.id}
                    onChange={(value) => {
                      const device = devices.find(d => d.id === value) || null;
                      setSelectedDevice(device);
                      resetSelections('device');
                    }}
                  >
                    {devices.map(device => (
                      <Select.Option key={device.id} value={device.id}>
                        {device.name}
                      </Select.Option>
                    ))}
                  </Select>
                </div>

                {/* 格式选择 */}
                {selectedDevice && (
                <div className="form-item">
                  <div className="form-label">录制格式</div>
                    <Select
                      placeholder="请选择录制格式"
                      style={{ width: '100%' }}
                      value={selectedFormat?.name}
                      onChange={(value) => {
                        const format = selectedDevice.formats.find(f => f.name === value) || null;
                        setSelectedFormat(format);
                        resetSelections('format');
                      }}
                    >
                      {selectedDevice.formats.map(format => (
                        <Select.Option key={format.name} value={format.name}>
                          {format.name}
                        </Select.Option>
                      ))}
                    </Select>
                  </div>
                )}

                {/* 帧率选择 */}
                {selectedFormat && (
                <div className="form-item">
                  <div className="form-label">帧率</div>
                    <Select
                      placeholder="请选择帧率"
                      style={{ width: '100%' }}
                      value={selectedFrameRate?.fps}
                      onChange={(value) => {
                        const frameRate = selectedFormat.frameRates.find(fr => fr.fps === value) || null;
                        setSelectedFrameRate(frameRate);
                        resetSelections('frameRate');
                      }}
                    >
                      {selectedFormat.frameRates.map(frameRate => (
                        <Select.Option key={frameRate.fps} value={frameRate.fps}>
                          {frameRate.fps} fps
                        </Select.Option>
                      ))}
                    </Select>
                  </div>
                )}

                {/* 设置选择 */}
                {selectedFrameRate && (
                <div className="form-item">
                  <div className="form-label">质量设置</div>
                    <Select
                      placeholder="请选择质量设置"
                      style={{ width: '100%' }}
                      value={selectedSetting?.name}
                      onChange={(value) => {
                        const setting = selectedFrameRate.settings.find(s => s.name === value) || null;
                        setSelectedSetting(setting);
                      }}
                    >
                      {selectedFrameRate.settings.map(setting => (
                        <Select.Option key={setting.name} value={setting.name}>
                          {setting.name} ({setting.bitrate} Mbps)
                        </Select.Option>
                      ))}
                    </Select>
                  </div>
                )}

                {/* 存储容量选择 */}
                <div className="form-item">
                  <div className="form-label">存储容量</div>
                  <Select
                    style={{ width: '100%' }}
                    value={storageCapacity}
                    onChange={setStorageCapacity}
                  >
                    {storageOptions.map(option => (
                      <Select.Option key={option.value} value={option.value}>
                        {option.label}
                      </Select.Option>
                    ))}
                  </Select>
                </div>
              </Space>
            </Card>
          </Col>

          <Col xs={24} sm={24} md={12} lg={12} xl={12} className="layout-col">
            <Card 
              title={
                <Space>
                  <IconStorage style={{ color: 'var(--feishu-primary)' }} />
                  计算结果
                </Space>
              }
              style={{ height: '100%' }}
              bordered={false}
              hoverable
            >
              {selectedSetting ? (
                <Space direction="vertical" size="large" style={{ width: '100%' }}>
                  {/* 当前配置信息 */}
                  <Alert
                    type="info"
                    icon={<IconSettings />}
                    content={
                      <div>
                        <div><span style={{ fontWeight: 'bold' }}>设备：</span>{selectedDevice?.name}</div>
                        <div><span style={{ fontWeight: 'bold' }}>格式：</span>{selectedFormat?.name}</div>
                        <div><span style={{ fontWeight: 'bold' }}>帧率：</span>{selectedFrameRate?.fps} fps</div>
                        <div><span style={{ fontWeight: 'bold' }}>设置：</span>{selectedSetting.name}</div>
                        <div><span style={{ fontWeight: 'bold' }}>分辨率：</span>{selectedSetting.resolution}</div>
                        <div><span style={{ fontWeight: 'bold' }}>码率：</span>{selectedSetting.bitrate} Mbps</div>
                      </div>
                    }
                    showIcon
                  />

                  <Divider />

                  {/* 计算结果 */}
          {/* 使用飞书布局规范的栅格系统 */}
          <Row gutter={[{ xs: 8, sm: 12, md: 16, lg: 20, xl: 24 }, { xs: 8, sm: 12, md: 16, lg: 20, xl: 24 }]} align="stretch">
            <Col xs={12} sm={12} md={12} lg={12} xl={12} style={{ display: 'flex' }}>
                  <Statistic
                    title={
                      <span>
                        <IconCalendar style={{ marginRight: 8 }} />
                        最大录制时间
                      </span>
                    }
                    value={maxRecordingTime}
                    suffix="分钟"
                    precision={0}
                    styleValue={{ color: '#165dff' }}
                  />
                    </Col>
                    <Col xs={12} sm={12} md={12} lg={12} xl={12}>
                  <Statistic
                    title={
                      <span>
                        <IconDashboard style={{ marginRight: 8 }} />
                        数据速率
                      </span>
                    }
                    value={dataRate}
                    suffix="MB/s"
                    precision={1}
                    styleValue={{ color: '#165dff' }}
                  />
                    </Col>
                  </Row>

                  <Statistic
                    title={
                      <span>
                        <IconFile style={{ marginRight: 8 }} />
                        1 分钟文件大小
                      </span>
                    }
                    value={fileSize.toFixed(0)}
                    suffix="MB"
                    style={{ textAlign: 'center' }}
                    styleValue={{ color: '#165dff', fontSize: '24px' }}
                  />

                  {/* 使用建议 */}
                  <Alert
                    type="success"
                    icon={<IconCheck />}
                    content={
                      <div>
                        <div><span style={{ fontWeight: 'bold' }}>推荐用途：</span>
                        {maxRecordingTime > 60 ? '长时间录制' : '短时间录制'}</div>
                        <div><span style={{ fontWeight: 'bold' }}>存储建议：</span>
                        {storageCapacity < 256 ? '建议使用更大容量存储设备' : '存储容量充足'}</div>
                      </div>
                    }
                    showIcon
                  />
                </Space>
              ) : (
                <div className="result-empty">
                  <IconExclamationCircle className="result-icon" style={{ fontSize: 48, color: '#86909c', marginBottom: 16 }} />
                  <Text type="secondary">请先选择录制配置</Text>
                </div>
              )}
            </Card>
          </Col>
        </Row>

        {/* 使用说明 */}
        {/* 使用说明卡片 */}
        <Card 
          title={
            <Space>
              <IconApps style={{ color: 'var(--feishu-primary)' }} />
              使用说明
            </Space>
          }
          style={{ marginTop: 24 }}
          bordered={false}
          hoverable
        >
          {/* 使用飞书布局规范的栅格系统 */}
          <Row gutter={[{ xs: 12, sm: 16, md: 20, lg: 24, xl: 24 }, { xs: 16, sm: 20, md: 24, lg: 32, xl: 40 }]} justify="center" align="stretch">
            <Col xs={24} sm={24} md={8} lg={8} xl={8}>
              <div className="guide-item">
                <IconVideoCamera className="guide-icon" />
                <Title heading={5} style={{ color: 'var(--feishu-text-1)', marginBottom: 8 }}>选择设备</Title>
                <Text type="secondary">
                  选择你要使用的录制设备，不同设备支持不同的录制格式和参数
                </Text>
              </div>
            </Col>
            <Col xs={24} sm={24} md={8} lg={8} xl={8}>
              <div className="guide-item">
                <IconClockCircle className="guide-icon" />
                <Title heading={5} style={{ color: 'var(--feishu-text-1)', marginBottom: 8 }}>配置参数</Title>
                <Text type="secondary">
                  根据需要选择录制格式、帧率和质量设置，不同配置影响文件大小
                </Text>
              </div>
            </Col>
            <Col xs={24} sm={24} md={8} lg={8} xl={8}>
              <div className="guide-item">
                <IconStorage className="guide-icon" />
                <Title heading={5} style={{ color: 'var(--feishu-text-1)', marginBottom: 8 }}>查看结果</Title>
                <Text type="secondary">
                  系统会自动计算最大录制时间、数据速率和文件大小等信息
                </Text>
              </div>
            </Col>
          </Row>
        </Card>
      </div>
    </div>
  );
}

export default App;